            :speed="2"
            primaryColor="#f3f3f3"
            secondaryColor="#ecebeb"
            v-if="ResizeScreen == 2"
            style="margin-top:80px;"
         >
            <circle cx="240" cy="50" r="50" />
            <rect x="90" y="120" rx="0" ry="0" width="84" height="15" />
            <rect x="90" y="140" rx="0" ry="0" width="140" height="1" />
            <rect x="90" y="160" rx="0" ry="0" width="50" height="7" />
