            <rect x="210" y="470" rx="0" ry="0" width="60" height="12" />
            <rect x="210" y="490" rx="0" ry="0" width="140" height="30" />
            
            
         </content-loader>
      </div>
      <div class="BXprofile" v-else>
         <div class="BxUpdateImage">
            <UpdateProfilePic v-model="avatar">
               <div slot="activator" style="cursor: pointer;">
                  <v-avatar size="150px" v-ripple v-if="!avatar" class="grey lighten-3 mb-3 img__wrap">
                    <img :src="GET_AdditionaLinfo.avatar" alt="avatar">
                    <span class="img__description">{{ $t('avatar') }}</span>
                  </v-avatar>
                  <v-avatar size="150px" v-ripple v-else class="mb-3">
                     <img :src="avatar.imageURL" alt="avatar">
                  </v-avatar>
               </div>
            </UpdateProfilePic>
            <v-slide-x-transition>
            <div v-if="avatar && saved == false">
               <v-btn class="primary" small @click="uploadImage" :loading="saving">Save Avatar</v-btn>
            </div>
            </v-slide-x-transition>
         </div>
         <div class="BxContentProf">
            <div class="BxContentProfbox">
               <PersonaLinfo
                  :Additional="GET_AdditionaLinfo"
                  :IsRoleId="CheckAuth"
               />

            </div>
         </div>
      </div>
   </div>

</template>

<script>
   import axios from 'axios'
   import UpdateProfilePic from '../../components/User-Profile/update-profile-pic/UpdateProfilePic.vue'
   import PersonaLinfo from '../../components/User-Profile/PersonaLinfo.vue'
   import { ContentLoader } from "vue-content-loader"
   export default {
      components:{
         UpdateProfilePic,
         PersonaLinfo,
         ContentLoader
      },
      data () {
         return {
            selectedFiles:null,
            currentAvatar:'',
            myData :null,
            avatar: null,
            saving: false,
            saved: false,
            windowWidth:0,
            windowHeigt:0,
            ResizeScreen: null
         }
      },
      
      mounted () {
         this.$nextTick( () => {
            window.addEventListener('resize', this.getWindowWidth);
            window.addEventListener('resize', this.getWindowHeight);
            this.getWindowWidth()
            this.getWindowHeight()
         })

         this.$store.dispatch("GET_PROFILEADDITIONAL");
      },
      methods: {
         uploadImage() {
            const formData = this.avatar.formData
            this.$store.dispatch('POST_UPDATEAVATAR', formData);
            this.saving = true
         },
         
         getWindowWidth(event){
            this.windowWidth = document.documentElement.clientWidth;

            if (this.windowWidth <= 599){
            //this for resolution phone only
            this.ResizeScreen = 1
            }else if(this.windowWidth <= 768){
               //this for resolution Ipad Potrait
            this.ResizeScreen = 2
            }else if(this.windowWidth >= 1024){
               //this for resolution Dekstop-up
            this.ResizeScreen = 3
            }
         },

         getWindowHeight(event){
            this.windowHeigt = document.documentElement.clientHeight;
         }
      },
      beforeDestroy() {
         window.removeEventListener('resize', this.getWindowWidth);
         window.removeEventListener('resize', this.getWindowHeight);
      },
      computed: {
         LOAD_AdditionaLinfo(){
            return this.$store.getters.PROFILE_ADDITIONAL
         },
         GET_AdditionaLinfo(){
			   return this.$store.state.DataAdditional
         },
         CckLoadDataProf(){
            return this.$store.state.CckLoadDataProf
         },
         CheckAuth(){
            return this.$store.state.UserRoleId
         },
         CheckUpdateAvatar(){
            return this.$store.getters.CCK_UPDATE_AVATAR
         }
      },
      created() {
         this.$store.dispatch("GET_PROFILEADDITIONAL");
         this.$store.dispatch("GET_PROFILE_TOKEN");
      },
      watch:{
         avatar: {
            handler: function() {
               this.saved = false
            },
            deep: true
         },

         CckLoadDataProf(val){
            if (val.status === 200){
               setTimeout(() => {
                  this.myData = 'OK';
               }, 1000)
            }
         },

         CheckUpdateAvatar (val){
            if (val == 200){
               this.saving = false
               this.saved = true 

               setTimeout(() => {
                  window.location.reload(true);
               }, 3000);
            }
         }
      },

   }
</script>

<style lang="scss">
    @import '../../styles/Profile.scss';

    /* relevant styles */
   .img__wrap {
      position: relative;
     
   }

   .img__description {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: #00000047;
      color: #fff;
      visibility: hidden;
      opacity: 0;
      display: flex;
      align-items: center;
      justify-content: center;

   /* transition effect. not necessary */
      transition: opacity .2s, visibility .2s;
   }

   .img__wrap:hover .img__description {
      visibility: visible;
      opacity: 1;
   }
</style>



